    <p>Easily save over the board chess games while mastering algebraic notation without&nbsp;trying.</p>

    <h2>
      <button class="button center" id="explainer-button">How Chess Notepad Works</button>
    </h2>
    <section id="explainer" class="chess-notepad__full-instructions" hidden>
      <button class="ex-button">
        <span class="visually-hidden">close</span>
        <svg viewBox="0 0 18 18">
