<script>
  export default {
    name: 'ChessInfo',
    props: {
      gameStarted: {
        type: Boolean,
        default: false
      }
    },
    mounted() {
      document.querySelectorAll('#clock-presets a').forEach(a => {
        a.addEventListener('click', this.handleClick);
      });
    },
    methods: {
      handleClick(e) {
        // TODO: Make this not reload SPA
        e.preventDefault();
        const buttonEl = e.target.classList.contains('button') ? e.target : e.target.parentElement;
        console.log(buttonEl);
        const time = buttonEl.getAttribute('href').replace('game/#', '');
        if (this.gameStarted) {
          if (window.confirm("Do you want to reset your current game?"))
            window.location.hash = time;
        } else
          window.location.hash = time;
          
        this.$emit('newGame', time);
      }
    }
  }
</script>