<template>
  <section class="chess-controls">
    <button 
      v-if="isPaused"
      title="Information"
      @click="$emit('info')"
    >
    <span class="visually-hidden">information</span>
    <svg viewBox="0 0 18 18">
      <path d="M6 6.96118C6.0166 5.0228 7.5715 3.4679 9.50988 3.4513C11.4817 3.43442 13.0804 5.04987 13.0804 7.0218C13.0804 9.96275 9.06059 9.79233 9.06059 11.6706M9.06059 14.9926V14.7414"/>
    </svg>

    </button>
    <button v-if="gameStarted" @click="$emit('togglePause')">
      <template v-if="isPaused">
        <span class="visually-hidden">play</span>
        
        <svg viewBox="0 0 18 18">
          <path d="M13.1198 9.04805L5.06618 15.0508L5.06618 3.0453L13.1198 9.04805Z" />
        </svg>

      </template>
      <template v-else>
        <span class="visually-hidden">pause</span>
        
        <svg viewBox="0 0 18 18">
          <path d="M13 4L13 14" />
          <path d="M5 4L5 14" />
        </svg>

      </template>
    </button>
    <template v-if="isPaused">
      <button v-if="gameStarted" @click="$emit('stop')">
        <svg viewBox="0 0 18 18">
          <rect x="4.10184" y="4" width="10" height="10" rx="1"/>
        </svg>

      </button>
      <button 
        v-else 
        title="New Clock Settings"
        @click="$emit('settings')"
      >
      <svg viewBox="0 0 18 18">
        <path d="M3.04865 5L14.9514 5" />
        <path d="M3.04865 13L14.9514 13" />
        <circle cx="11.5354" cy="4.96649" r="1.30841" />
        <circle cx="6.40942" cy="13.012" r="1.30841" />
      </svg>

      </button>
    
    </template>
  </section>
</template>

<script>
  export default {
    name: 'ChessControls',
    props: {
      isPaused: {
        type: Boolean,
        default: false
      },
      gameStarted: {
        type: Boolean,
        default: false
      }
    }
  }
</script>

<style>
.chess-controls {
  display: flex;
}

.chess-controls button {
  border: 0;
  background: var(--magic);
  width: 100%;
  font-size: 30px;
}
</style>